import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			cateMaskState: 0, //分类面板展开状态
			headerPosition:"fixed",
			headerTop:"0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 0, 
			cateId: 0, //已选三级分类id
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			cateList: [],
			goodsList: [],
			sid:0,
			salesOrder:'',
			source: 0,
			addressList: [],
			scrollHeight: 100,
			tabScrollTop: 0,
			contentText: {
				contentdown: '上拉加载更多',
				contentrefresh: '加载中',
				contentnomore: '我是有底线的'
			},
			page:1,
			limit:20,
			status: 'more',
			keyword:'',
			type:0,
		};
	},
	
	onLoad(options){
		
		console.log('页面',options);
		this.sid = options.sid
		this.keyword = options.keyword
		getApp().user_api(true);
		this.calcSize();
		
	},
	onShow(){
		this.page = 1;
		this.goodsList = [];
		this.getProductList();
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh(){
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	//加载更多
	onReachBottom(){
		// this.loadData();
	},
	methods: {
		
		getProductList(){
			let self = this;
			// console.log('排序',value);
			
			var path = '/gzh/store_api/get_user_collect_product'
			var data = {
				page:self.page,
				limit:self.limit
			};
			
			getApp().http(path, data, function(res) {
				console.log('详情',res);
				uni.hideLoading();
				
				let list = res;
				if (list.length > 0) {
					console.log('总条数',list.length)
					
					self.goodsList = self.goodsList.concat(list);
					self.status = list.length >= self.limit ? "more" : "";
					if (self.status == "more") {
						self.page += 1;
					}
					
					// self.status = list.length <= self.limit ? "more" : "";
					// if (self.status == "more") {
					// 	self.page += 1;
						
					// 	self.goodsList = self.goodsList.concat(list);
					// }
				} else {
					self.status = "";
				}
			})
		},
		//筛选点击
		tabClick(index){
			let self = this;
			console.log('点击',index)
			
			self.filterIndex = index;
			
			if(index === 2){
				this.priceOrder = this.priceOrder === 1 ? 2: 1;
			}else{
				this.priceOrder = 0;
			}
			
			uni.showLoading({
				title: '正在加载'
			})
			
			if(self.filterIndex == 0){
				console.log('优惠券')
				self.page = 1;
				self.goodsList = []
				self.type = 0
				self.getProductList()
			}else if(self.filterIndex == 1){
				self.page = 1;
				self.goodsList = []
				self.type = 1
				self.getProductList()
				console.log('我的')
			}
		},
		
		//删除
		userCoupon(item){
			let self = this;
			let pid = item.pid
			let path = '/gzh/store_api/get_user_collect_product_del'
			
			console.log('删除收藏',item)
			
			let data = {
				pid:pid
			};
			getApp().http(path, data, function(res) {
				console.log('领取',res)
				if(res.code == 200){
					self.goodsList = [],
					self.page = 1,
					self.getProductList();
					getApp().showTip(res.msg, "success")
				}else{
					getApp().showTip(res.msg,'none')
				}
			},true)
			
		},
		
		indexUrl(icon){
			let http = icon.indexOf('http')
			if(http == -1){
				return this.webUrl +'/'+icon
			}else{
				return icon;
			}
		},
		
		navClick(item){
			console.log('跳转',item);
			uni.navigateTo({
				url: "/pages/product/product?id="+item.pid
			})
		},
		
		// 到底触发
		scroAddr(){
			let self = this;
			if(self.status){
				self.getProductList();
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize(){
			var self = this;
			uni.getSystemInfo({
				success: function(res) { // res - 各种参数
					let info = uni.createSelectorQuery().select(".content");
					self.scrollHeight = res.windowHeight
					// self.scrollHeight = 200
					// console.log(info);
					// console.log('啊啊啊啊',res);
				}
			});
		},
		
	},
}
